import CardComp from "../components/CardComp";
import ProductCard from "../components/ProductCard";
import "../styles/HomePage.css";

const HomePage = () => {
  return (
    <>
      <div className="home-page">
        <div className="home-img">
          <img src="/images/home-page2.webp" alt="homeImg" />
        </div>

        <CardComp name="Popular" viewName="see all" />
        <ProductCard categoryName="best seller" />

        <CardComp name="just lunched" viewName="see all" />
        <ProductCard categoryName="just lunched" />

        <CardComp name="fit foods" viewName="see all" />
        <ProductCard categoryName="fit foods" />

        <CardComp name="your level" />
        <div className="shop-by-level-cont">
          <div className="shop-card">
            <div className="shop-card-inner">
              <div className="shop-card-front">
                <p>Beginner</p>
              </div>
              <div className="shop-card-back">
                <p>For all buillding health buffs who are starting out on fitness journey</p>
              </div>
            </div>
          </div>
          <div className="shop-card">
            <div className="shop-card-inner">
              <div className="shop-card-front">
                <p>Intermidiate</p>
              </div>
              <div className="shop-card-back">
                <p>In the middle of your fitness journey and looking to inprove? This range for you.</p>
              </div>
            </div>
          </div>
          <div className="shop-card">
            <div className="shop-card-inner">
              <div className="shop-card-front">
                <p>Advance</p>
              </div>
              <div className="shop-card-back">
                <p>In the middle of your fitness journey and looking to inprove? This range for you.</p>
              </div>
            </div>
          </div>
        </div>

        <div className="vdo-section">
          <CardComp name="#phirseziddkar" />
          <div className="vdos">
            <video controls>
              <source src="/videos/vdo2.mp4" type="video/mp4"/>
            </video>
          </div>
        </div>
      </div>
    </>
  );
};

export default HomePage;
