import '../styles/CardComp.css';
import {Link} from 'react-router-dom'

const CardComp = ({name, viewName}) => {

  return (
    <>
    <div className='popular_page'>
        <div className="popular">
            <h1> {name} </h1>
        </div>
        <Link to="/allproducts" className="see_all">{viewName}</Link>
    </div>
    </>
  )
}

export default CardComp
