import "../styles/ProductCard.css";
import axios from "axios";
import React, { useEffect } from "react";
import { useState } from "react";
import { Link } from "react-router-dom";
import Currency from "./Currency";
import { IoCart } from "react-icons/io5";
import { useCart } from "../context/cart";
import toast from 'react-hot-toast'

const ProductCard = ({ categoryName }) => {
  const [featuredData, setFeaturedData] = useState([]);
  const [cart, setCart] = useCart();

  const getFeaturedData = async () => {
    try {
      const res = await axios.get(
        `/api/v1/product/get-product`
      );
      const data = res.data.products;
      const filteredData = data.filter(
        (item) =>
          item.category?.name === categoryName && item.isFeatured === true
      );

      setFeaturedData(filteredData);
    } catch (error) {
    }
  };

  useEffect(() => {
    getFeaturedData();
    // eslint-disable-next-line
  }, []);

  return (
    <>
      <div className="pr-card-page">
        <div className="pr-card">
          {featuredData?.map((p) => {
            return (
              <>
                <Link
                  to={`/singlepage/${p.slug}`}
                  key={p._id}
                  className="card-link"
                >
                  <div className="card">
                    <div className="card-img">
                      <img
                        src={`/api/v1/product/product-photo/${p._id}`}
                        alt={p.name}
                      />
                    </div>
                    <div className="card-info">
                      <p className="text-title"> {p.name} </p>
                      <p className="text-body">
                        {p.description.substring(0, 30)}...
                      </p>
                    </div>
                    <div className="card-footer">
                      <span className="text-title">
                        {<Currency price={p.price} />}
                      </span>
                      <Link className="card-button-link">
                        <div
                          className="card-button"
                        >
                          <IoCart 
                            className="cart-icon" 
                            onClick={() => {
                              setCart([...cart, p])
                              localStorage.setItem('cart', JSON.stringify([...cart, p]));
                              toast.success("Product add to cart")
                            }} />
                        </div>
                      </Link>
                    </div>
                  </div>
                </Link>
              </>
            );
          })}
        </div>
      </div>
    </>
  );
};

export default ProductCard;
